#content {
  position: relative;
  .add-contact {
    float: right;
    min-width: 300px;
    position: absolute;
    right: 0;
    top: 0;
    input[type="file"] {
      opacity: 0;
      width: 0;
      height: 0;
      display: block; } }
  .filter {
    height: 70px;
    padding: 0 25px;
    .check-all {
      margin-right: 0 !important;
      padding-top: 27px;
      width: 20px; }
    .form {
      display: inline-block;
      vertical-align: top;
      .col {
        margin-right: 18px;
        width: 210px;
        label {
          color: #161616;
          display: block;
          font-size: 11px;
          height: 18px; }
        input.search {
          background: img('/old/search.png', 'right center no-repeat' ,'');
          padding-right: 30px; }
        &:last-child {
          margin-right: 0; } } } }
  .contacts-list .contact {
    &:first-child {
      padding-top: 0;
      .actions {
        top: 5px; } }
    &:hover {
      background: #edf4fa; }
    height: 165px;
    padding: 10px 25px 25px;
    position: relative;
    &.long {
      height: auto; }
    hr {
      border: 1px solid #d9d9d9;
      border-top: 0;
      bottom: 0;
      left: 25px;
      position: absolute;
      right: 25px; }
    .actions {
      position: absolute;
      right: 25px;
      top: 20px;
      a {
        &.edit {
          color: #161616;
          padding-right: 20px; }
        &.remove {
          color: #727272; } } }
    .title {
      font-size: 14px;
      font-weight: 700;
      line-height: 36px;
      min-height: 36px; }
    .email {
      height: 20px;
      .label {
        color: #161616;
        width: 60px; } }
    .cert {
      padding-top: 8px;
      .cert-box {
        height: 53px;
        overflow: hidden; } }
    &.long .cert-box {
      height: auto; }
    .toggler {
      bottom: 5px;
      height: 20px;
      left: 25px;
      position: absolute;
      right: 25px;
      a {
        color: #000;
        cursor: pointer;
        font-size: 11px;
        font-weight: 700;
        text-decoration: underline;
        &:hover {
          text-decoration: none; } } }
    .cert {
      .cert-title {
        font-weight: 700;
        height: 20px; }
      table.list {
        width: 100%;
        tbody tr {
          height: 22px;
          td {
            padding: 0 2px;
            span {
              &.active {
                color: #161616; }
              &.expire {
                color: #f4484a; } }
            a {
              &.download {
                background: img('/old/download.png', 'no-repeat' ,'');
                display: inline-block;
                height: 13px;
                margin-right: 10px;
                width: 14px; }
              &.remove {
                background: img('/old/remove.png', 'no-repeat' ,'');
                display: inline-block;
                height: 12px;
                width: 13px; } } } } } } }
  .more-cert {
    height: 75px;
    padding-top: 28px;
    text-align: center; } }

