#login {
  margin: 35px auto 0;
  width: 505px;
  h1 {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 30px;
    text-align: center; }
  .title {
    -webkit-border-radius: 3px 0 0;
    background: #FFF;
    border: 1px solid #d5d5d5;
    border-bottom: 0;
    border-radius: 3px 0 0;
    display: inline-block;
    height: 46px;
    line-height: 46px;
    padding: 0 15px; }
  .box {
    -webkit-border-radius: 0 3px 3px 3px;
    background: #FFF;
    border: 1px solid #d5d5d5;
    border-radius: 0 3px 3px 3px;
    margin-top: -1px;
    .login-area {
      padding: 40px 15px 0;
      .row {
        height: 50px; }
      label {
        line-height: 28px;
        padding-right: 20px;
        text-align: right;
        width: 158px; }
      .value {
        position: relative;
        width: 310px;
        .checkBubble {
          background: img('/old/checkBubble.png', 'no-repeat' ,'');
          font-size: 11px;
          height: 49px;
          line-height: 48px;
          padding-left: 15px;
          position: absolute;
          right: -155px;
          top: -10px;
          width: 148px; } }
      .restore a {
        color: #7b7b7b;
        font-size: 11px; }
      .sbmt {
        margin-top: 25px;
        padding-left: 160px;
        label {
          line-height: 32px;
          width: 80px;
          .button-checkbox, input[type=checkbox] {
            margin-right: 10px; } } } }
    .bottom {
      border-top: 1px solid #e7e7e7;
      height: 50px;
      margin-top: 25px;
      a {
        line-height: 50px;
        margin: 0 20px; } } }
  .error-mess {
    background: img('/old/warning.png', '10px center no-repeat' ,'#f3d2d2');
    color: #c12a2b;
    height: 37px;
    line-height: 37px;
    margin-top: 13px;
    padding: 0 10px 0 50px; } }

